import React, { Component } from 'react';
import { BrowserRouter as Router } from 'react-router-dom';
import { HashLink as Link } from 'react-router-hash-link';

class Navigation extends Component {
  constructor (props) {
    super(props);

    this.state = {
      opened: false
    };
  }

  clickHandler () {
    // [NOTE] 省略して !this.state.opended などを this.setState に書いても動作しないため
    const newOpenedState = this.state.opened ? false : true;

    this.setState({
      opened: newOpenedState
    });
  }

  render () {
    const lists = datas => {
      const list = datas.map((data, i) => (
        <li key={i} className="sidebar-nav-item">
          <Link className="js-scroll-trigger" to={data.to} onClick={this.clickHandler.bind(this)}>{data.title}</Link>
        </li>
      ));

      return list;
    };

    return (
      <Router>
        <div>
          <Link className={'menu-toggle rounded' + (this.state.opened ? ' active' : '')} onClick={this.clickHandler.bind(this)} to="#">
            <i className={'fas ' + (this.state.opened ? 'fa-times' : 'fa-bars')}></i>
          </Link>
            <nav id="sidebar-wrapper" className={this.state.opened ? 'active' : ''} onClick={this.clickHandler.bind(this)} style={{
              backgroundColor: '#000',
              borderLeft: '1px solid #ecb807'
            }}>
            <ul className="sidebar-nav">
              {lists([{
                to: '#top',
                title: 'Top'
              }, {
                to: '#about',
                title: 'About'
              }, {
                to: '#services',
                title: 'Services'
              }, {
                to: '#portfolio',
                title: 'Portfolio'
              }, {
                to: '#contact',
                title: 'Contact'
              }])}
            </ul>
          </nav>
        </div>
      </Router>
    );
  }
}

export default Navigation;
