import React, { Component } from 'react';
import { BrowserRouter as Router } from 'react-router-dom';
import { HashLink as Link } from 'react-router-hash-link';
import portfolioData from './portfolioData';
import Lightbox from 'react-images';

class Portfolio extends Component {
  constructor (props) {
    super(props);

    this.state = {
      lightboxIsOpen: false,
      currentImage: 0
    };
  }

  gotoPrevious () {
    this.setState({
      currentImage: this.state.currentImage - 1
    });
  }

  gotoNext () {
    this.setState({
      currentImage: this.state.currentImage + 1
    });
  }

  closeLightbox () {
    this.setState({
      lightboxIsOpen: false
    });
  }

  openLightbox (currentImage) {
    this.setState({
      lightboxIsOpen: true,
      currentImage
    });
  }

  clickThumbnail (currentImage) {
    this.setState({
      currentImage
    });
  }

  render () {
    const items = datas => {
      const item = datas.map((data, i) => (
        <div key={i} className="col-lg-6">
          <Link className="portfolio-item" to="#" onClick={this.openLightbox.bind(this, i)}>
            <span className="caption">
              <span className="caption-content">
                <h2>{data.caption}</h2>
                <p className="mb-0">{data.description}</p>
              </span>
            </span>
            <img className="img-fluid" src={data.image} alt={data.caption} />
          </Link>
        </div>
      ));

      return item;
    };

    const lightboxData = portfolioData.map((data, index) => ({
      src: data.image,
      caption: data.caption,
      alt: data.caption
    }));

    return (
      <Router>
        <section className="content-section" id="portfolio">
          <div className="container">
            <div className="content-section-heading text-center">
              <h3 className="text-secondary mb-0">Portfolio</h3>
              <h2 className="mb-5">Recent Works</h2>
            </div>
            <div className="row no-gutters">
              {items(portfolioData)}
              <Lightbox
                backdropClosesModal={true}
                showThumbnails={true}
                images={lightboxData}
                currentImage={this.state.currentImage}
                isOpen={this.state.lightboxIsOpen}
                onClickPrev={this.gotoPrevious.bind(this)}
                onClickNext={this.gotoNext.bind(this)}
                onClose={this.closeLightbox.bind(this)}
                onClickThumbnail={this.clickThumbnail.bind(this)}
              />
            </div>
          </div>
        </section>
      </Router>
    );
  }
}

export default Portfolio;
