import React, { Component } from 'react';
import { BrowserRouter as Router } from 'react-router-dom';
import { HashLink as Link } from 'react-router-hash-link';

class ScrollToTop extends Component {
  constructor (props) {
    super(props);

    this.state = {
      opacity: 0,
      pointerEvents: 'none'
    };
  }

  componentDidMount () {
    document.addEventListener('scroll', () => {
      // NOTE: document.documentElement.scrollTop では iOS で判定できなかったため
      const scrollDistance = document.documentElement.getBoundingClientRect().top * -1;

      if (scrollDistance > document.documentElement.clientHeight) {
        this.setState({
          opacity: 1,
          pointerEvents: 'auto'
        });
      } else {
        this.setState({
          opacity: 0,
          pointerEvents: 'none'
        });
      }
    });
  }

  clickHandler () {
    // NOTE: iOS と PC でスクロール位置の計算が異なるため
    const targetElement = document.documentElement.scrollTop > 0 ? document.documentElement : document.body;

    const step = targetElement.scrollTop / 25;

    const anime = () => {
      if (targetElement.scrollTop <= 0) return;

      targetElement.scrollTop -= step;

      requestAnimationFrame(anime);
    };

    anime();
  }

  render () {
    return (
      <Router>
        <Link className="scroll-to-top rounded js-scroll-trigger" to="#" onClick={this.clickHandler.bind(this)} style={{
          opacity: this.state.opacity,
          pointerEvents: this.state.pointerEvents
        }}>
          <i className="fas fa-angle-up"></i>
        </Link>
      </Router>
    );
  }
}

export default ScrollToTop;
